import {createStore} from 'redux';
import {Provider} from 'react-redux';

const categData = {
  categoryData: [
    {
      label: 'Все товары',
      description: 'Все товары',
      children: [
        {
          label: 'Продукты',
          description: 'Продукты',
          children: [
            {
              label: 'Фрукты',
              description: 'Фрукты',
              children: [],
            },
            {
              label: 'Овощи',
              description: 'Овощи',
              children: [],
            },
            {
              label: 'Молочные изделия',
              description: 'Молочные изделия',
              children: [],
            },
            {
              label: 'Напитки',
              description: 'Напитки',
              children: [
                {
                  label: 'Алкогольные',
                  description: 'Алкогольные',
                  children: [
                    {
                      label: 'Крепкие',
                      description: 'Крепкие',
                      children: []
                    },
                    {
                      label: 'Слабоалкогольные',
                      description: 'Слабоалкогольные',
                      children: [],
                    }
                  ]
                },
                {
                  label: 'Безалкогольные',
                  description: 'Безалкогольные',
                  children: [],
                },
              ]
            },
          ]
        },
        {
          label: 'Велосипеды',
          description: 'Все товары',
          children: [],
        },
        {
          label: 'Одежда',
          description: 'Все товары',
          children: [],
        }
      ]
    }
  ]
}

const recursive = (arr, createdCategory) => {
  let a;

  const c = (arr, createdCategory) => {
    for (const item of arr) {
      if (!createdCategory.parent.includes(item.label)) {
        c(item.children, createdCategory)
      }
      if (createdCategory.parent.includes(item.label)) {
        a = item.children;
        return;
      }
    }
  }

  c(arr, createdCategory);

  return a;

};

export const categoryReducer = (state = categData, action) => {

  switch (action.type) {
    case 'add__category':
      return {
        ...state,
        category: [...state.categoryData],
        action: recursive(state.categoryData, {label: action.label, parent: action.parent}).push({
          label: action.label,
          description: action.description,
          children: [],
          parent: action.parent
        })
      }

    default:
      return state;
  }
};
